.hero-home-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.hero-p,
.hero-h1 {
  color: #fff;
  line-height: 1.2;
  font-family: 'Oswald', sans-serif;
}

.hero-p {
  font-size: 3.7rem;
  font-weight: 600;
  font-family: 'Oswald', sans-serif;
}

.hero-h1 {
  font-size: 18rem;
  font-family: 'Oswald', sans-serif;
}

.underline {
  width: 34%;
  margin-top: 1.8rem;
  border: 1px solid #fecb1c;
}

.hero-description {
  margin-top: 1.6rem;
  font-style: italic;
  font-size: 1.5rem;
  font-weight: 400;
  /* opacity: .8; */
  color: #fff;
  width: 30%;
  text-align: center;
  line-height: 1.5;
  letter-spacing: 0.1rem;
}


.hero-home-btn:link,
.hero-home-btn:visited {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  color: #fff;
  background-color: transparent;
  border: 1px solid #fecb1c;
  border-radius: 0.4rem;
  padding: 1.4rem 3rem;
  text-decoration: none;
  margin-top: 1.6rem;
  font-size: 1.5rem;
  transition: 0.3s ease-in-out;
}


.hero-home-btn:hover,
.hero-home-btn:active {
  background-color: #fecb1c;
}
.work-container {
  padding: 9.6rem 12rem;
  height: 100%;
  margin: 0 auto;
  background-color: #fecb1c;

}

.work-icon {
  position: relative;
}

.work-icon svg {
  height: 24rem;
  width: 24rem;
  fill: rgba(0, 0, 0, 0.1);
}

.work-icon span {
  text-transform: uppercase;
  font-size: 10rem;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: 2px;
  font-family: oswald, sans-serif;
  position: absolute;
  left: 6rem;
}

.work-description {
  font-family: 'Raleway', sans-serif;
  position: absolute;
  top: 12rem;
  left: 6rem;
  font-style: italic;
  color: #000000;
  font-size: 1.5rem;
  line-height: 1.5;
  font-weight: 600;
  letter-spacing: .02rem;
  width: 60%;
}

