.work-desc-container {
  width: 84%;
  margin: 7rem auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.work-desc {
  font-style: italic;
  color: #000;
  font-size: 1.6rem;
  line-height: 1.5;
  letter-spacing: 1px;
  font-weight: 300;
}

.work-desc span {
  color: #fff;
}


.carousel-container {
  width: 80%;
  /* Adjust to your desired width */
  margin: 8rem auto;
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
}

.image {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 8px;
}

.image:hover {
  cursor: pointer;
}

.direction {
  background: rgba(128, 128, 128, 0.289);
  position: relative;
}

.dir-icon-left,.dir-icon-right {
  height: 4rem;
  width: 3rem;
  border-radius: 5px;
  padding: 1rem 0.5rem;
  background: rgba(255, 255, 255, 0.289);
  position: absolute;
  transition: 0.3s ease-in-out;
}

.dir-icon-left {
  position: absolute;
  top: 12rem;
  left: -3rem;
}

.dir-icon-right {
  top: 12rem;
  right: -2.5rem;
}

.dir-icon-left:hover,
.dir-icon-right:hover {
  cursor: pointer;
  background: rgba(128, 128, 128, 0.289);
}
.social-media-container {
  display: flex;
  flex-direction: row;
  grid-gap: 1rem;
  gap: 1rem;
}

.icon {
  background: #fff;
  padding: 1rem;
  border-radius: 50%; 
}

.icon {
  height: 5rem;
  width: 5rem;
}


.row {
  height: 10px;
  margin: 0 5rem;
  padding: 10px;
}

.darkColor {
  background-color: #000000;
  height: 1px;
}

.about-section {
  height: 180vh;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4)), url(/React-static-page/static/media/about-studio.2ee99d17.jpg);

  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
}

.about-container {
  width: 70%;
  margin: 0 auto;
}

.about-text {
  margin-bottom: 5rem;
}

.Title {
  font-family: oswald, sans-serif;
  padding-top: 15rem;
  font-size: 10rem;
  line-height: 1.2;
  letter-spacing: 2px;
  color: #fff;
  margin-bottom: 2rem;
}

.desc {
  color: #fff;
  font-size: 1.4rem;
  font-style: italic;
  width: 60%;
  letter-spacing: 0.7px;
  margin-bottom: 6rem;
  /* font-weight: bold; */
}

.customDivider {
  background-color: #fecb1c;
  height: 1px;
}

.about-employees {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.employee {
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  justify-items: center;
  align-items: center;
}

.image {
  display: grid;
  place-items: center;
}

.image1 {
  width: 70%;
  border-radius: 50%;
}

.employee-name {
  font-size: 2.4rem;
  color: #fff;
  letter-spacing: 0.8px;
  margin-bottom: -10px;
}

.profile {
  color: #fecb1c;
  font-size: 1.4rem;
  font-style: italic;
  font-weight: bold;
  letter-spacing: 1px;

}

.what-we-do {
  display: flex;
  justify-content: space-between;
  color: #fff;
  margin: 8rem 2rem 0 2rem;
  /* width: 95%; */
  align-items: center;
}

.what-we-do-h h4 {
  font-family: oswald,sans-serif;
  font-size: 2.4rem;
  line-height: 1.4;
  letter-spacing: 0.4px;
  word-break: keep-all;
}

.what-we-do-icon {
  /* vertical-align: middle; */
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 3rem;
  row-gap: 3rem;
}

.what-we-do-icon p {
  font-size: 1.6rem;
  font-weight: 600;
}



.Contact-section {

  background: #fecb1c;
}

.Contact-container {
  width: 70%;
  margin: 0 auto;
  padding: 10rem 0;
  position: relative;
}


.title-icon {
  position: absolute;
  left: 25rem;
  bottom: 56rem;
  z-index: 100;
  opacity: 15%;
  /* right: 10px; */
}

.Contact-title {
  font-family: oswald, sans-serif;
  font-size: 10rem;
  text-align: center;
  z-index: 200;
  text-transform: uppercase;
  color: #000;
}


.contact-description {
  text-align: center;
  font-style: italic;
  font-weight: 600;
  letter-spacing: 0.7px;

  line-height: 1.8;
}

.contact-dec {
  font-size: 2rem;
  margin: 6rem 15rem 4rem 15rem;
  padding-left: 10rem;
