}

.contact-mail {
  margin-top: 10rem;
}

.contact-mail a {
  font-family: oswald, sans-serif;
  text-decoration: none;
  color: #000;
  font-size: 4rem;
  font-weight: 700;
}

.contact-mail:hover {
  text-decoration: underline;
  text-decoration-thickness: 5px;
}

.contact-details {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 3rem;
}

.message-icon {
  display: grid;
  justify-content: center;
  height: 6.4rem;
  width: 6.4rem;
  margin-bottom: 2rem;

}

